import { Injectable } from '@angular/core';
import { AngularFireDatabase } from 'angularfire2/database';
import 'rxjs/add/operator/map';
@Injectable()
export class ContactProvider {
  private PATH = 'contacts/';
  private PATHM = 'mantimen/';
  private PATHD = 'mantimen/medicamentos/';
  private PATHG = 'gatinhos/';
  constructor(private db: AngularFireDatabase) {
  }

  getAll() {
    return this.db.list(this.PATH, ref => ref.orderByChild('name'))
      .snapshotChanges()
      .map(changes => {
        return changes.map(c => ({ key: c.payload.key, ...c.payload.val() }));
      })
  }

  getAllM() {
    return this.db.list(this.PATHM, ref => ref.orderByChild('name'))
      .snapshotChanges()
      .map(changes => {
        return changes.map(c => ({ key: c.payload.key, ...c.payload.val() }));
      })
  }

  getAllD() {
    return this.db.list(this.PATHD, ref => ref.orderByChild('name'))
      .snapshotChanges()
      .map(changes => {
        return changes.map(c => ({ key: c.payload.key, ...c.payload.val() }));
      })
  }

  getAllG() {
    return this.db.list(this.PATHG, ref => ref.orderByChild('name'))
      .snapshotChanges()
      .map(changes => {
        return changes.map(c => ({ key: c.payload.key, ...c.payload.val() }));
      })
  }

  get(key: string) {
    return this.db.object(this.PATH + key).snapshotChanges()
      .map(c => {
        return { key: c.key, ...c.payload.val() };
      });
  }

  save(contact: any) {
    return new Promise((resolve, reject) => {
      if (contact.key) {
        this.db.list(this.PATH)
          .update(contact.key, { name: contact.name, tel: contact.tel })
          .then(() => resolve())
          .catch((e) => reject(e));
      } else {
        this.db.list(this.PATH)
          .push({ name: contact.name, tel: contact.tel })
          .then(() => resolve());
      }
    })
  }

  saveD(drug: any) {
    return new Promise((resolve, reject) => {
      if (drug.key) {
        this.db.list(this.PATHD)
          .update(drug.key, { nome: drug.nome, funcao: drug.funcao })
          .then(() => resolve())
          .catch((e) => reject(e));
      } else {
        this.db.list(this.PATHD)
          .push({ nome: drug.nome, funcao: drug.funcao })
          .then(() => resolve());
      }
    })
  }

  saveG(cat: any) {
    return new Promise((resolve, reject) => {
      if (cat.key) {
        this.db.list(this.PATHG)
          .update(cat.key, { nome: cat.nome, idade: cat.idade, dono: cat.dono, doenca: cat.doenca })
          .then(() => resolve())
          .catch((e) => reject(e));
      } else {
        this.db.list(this.PATHG)
          .push({ nome: cat.nome, idade: cat.idade, dono: cat.dono, doenca: cat.doenca })
          .then(() => resolve());
      }
    })
  }

  remove(key: string) {
    return this.db.list(this.PATH).remove(key);
  }
  removeD(key: string) {
    return this.db.list(this.PATHD).remove(key);
  }
  removeG(key: string) {
    return this.db.list(this.PATHG).remove(key);
  }
}



